.categoryDisplay ul li {
	list-style: none;
	text-align: left;
}

.categoryDisplay {
	margin-top: 3rem;
	margin-left: auto;
}

.rct-collapse-btn *,
icon * {
	font-size: 25px;
	width: 30px;
}

.rct-checkbox * {
	font-size: 22px;
	width: 25px;
}

.modal-header {
	padding: 5px 15px;
	background: #138496;
	color: #eee;
}

.buttonContainer {
	display: flex;
	justify-content: flex-end;
}

button {
	margin: 10px;
}

.rct-icon-parent-close,
.rct-node-icon {
	visibility: hidden;
	padding: 0 1px;
}

.showCats {
	/* background: #444; */
	margin-top: 2rem;
	margin-bottom: 3rem;
}

body {
	height: 100vh;
}

